@charset "UTF-8";/*
	* ------------------------------------------------
	* [Generic] - Box-Sizing
	* ------------------------------------------------
	*/html {
  box-sizing: border-box;
}

*,*:before,*:after {
  box-sizing: inherit;
}/*
	* ------------------------------------------------
	* [Generic] - Normalize
	* ------------------------------------------------
	*/html {
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article,aside,footer,header,nav,section {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

figcaption,figure,main {
  display: block;
}

figure {
  margin: 1em 40px;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,strong {
  font-weight: inherit;
}

b,strong {
  font-weight: bolder;
}

code,kbd,samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

dfn {
  font-style: italic;
}

mark {
  background-color: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub,sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

audio,video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

img {
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

button,input,optgroup,select,textarea {
  font-family: sans-serif;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,input {
  overflow: visible;
}

button,select {
  text-transform: none;
}

button,html [type="button"],[type="reset"],[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,[type="button"]::-moz-focus-inner,[type="reset"]::-moz-focus-inner,[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,[type="button"]:-moz-focusring,[type="reset"]:-moz-focusring,[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  display: inline-block;
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="checkbox"],[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button,[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-cancel-button,[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details,menu {
  display: block;
}

summary {
  display: list-item;
}

canvas {
  display: inline-block;
}

template {
  display: none;
}

[hidden] {
  display: none;
}/*
	* ------------------------------------------------
	* [Generic] - Reset
	* ------------------------------------------------
	*/body,h1,h2,h3,h4,h5,h6,blockquote,p,pre,dl,dd,ol,ul,figure,hr,fieldset,legend {
  margin: 0;
  padding: 0;
}

li > ol,li > ul {
  margin-bottom: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset {
  min-width: 0;
  border: 0;
}

.font-thin,[font-thin] {
  font-weight: 100;
}

.font-extra-light,[font-extra-light] {
  font-weight: 200;
}

.font-light,[font-light] {
  font-weight: 300;
}

.font-regular,[font-regular] {
  font-weight: 400;
}

.font-medium,[font-medium] {
  font-weight: 500;
}

.font-semi-bold,[font-semi-bold] {
  font-weight: 600;
}

.font-bold,[font-bold] {
  font-weight: 700;
}

.font-extra-bold,[font-extra-bold] {
  font-weight: 800;
}

.font-black,[font-black] {
  font-weight: 900;
}

.text-underline {
  text-decoration: underline;
}

.pre-wrap,[pre-wrap] {
  white-space: pre-wrap;
}/*
	* ------------------------------------------------
	* [Common] - Margin Rhythm
	* ------------------------------------------------
	*/address,h1,h2,h3,h4,h5,h6,blockquote,p,pre,dl,ol,ul,figure,hr,table,fieldset {
  margin-bottom: 15px;
}

dd,ol,ul {
  margin-left: 15px;
}/*
	* ------------------------------------------------
	* [Common] - Simple page-level setup.
	* ------------------------------------------------
	*/html {
  font-size: 1em;
  line-height: 24px;
  overflow-y: scroll;
  min-height: 100%;
}

body {
  height: 100%;
}/*
