}

.l-flex-start-end,[l-flex-start-end] {
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
}

.l-flex-start-center,[l-flex-start-center] {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.l-flex-end,[l-flex-end] {
  display: flex;
  justify-content: flex-end;
}

.l-flex-end-start,[l-flex-end-start] {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
}

.l-flex-end-end,[l-flex-end-end] {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}

.l-flex-end-center,[l-flex-end-center] {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.l-flex-center,[l-flex-center] {
  display: flex;
  justify-content: center;
}

.l-flex-center-start,[l-flex-center-start] {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.l-flex-center-end,[l-flex-center-end] {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.l-flex-center-center,[l-flex-center-center] {
  display: flex;
  justify-content: center;
  align-items: center;
}/*
*
* MY CSS
*
*/body {
  font-family: 'Helvetica';
  font-size: 14px;
  padding: 15px;
}

ol,ul {
  margin: 0;
  list-style: none;
}

p {
  width: 100%;
  text-align: left;
}

.wrapper {
  display: flex;
  align-items: flex-start;
}

.list-flex {
  width: 200px;
  margin: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column;
}

.list-flex__item {
  display: flex;
  width: 100%;
  height: 70px;
  padding: 7.5px;
}

.list-flex__item .list-childs {
  flex: 1 1 60px;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
}

.list-flex__item .list-childs + .info {
  flex: 1 1 50%;
  margin-left: 5px;
}

.list-flex__item:nth-child(0) .list-childs {
  justify-content: flex-start;
  align-items: flex-start;
}

.list-flex__item:nth-child(1) .list-childs {
  justify-content: flex-start;
  align-items: center;
}

.list-flex__item:nth-child(2) .list-childs {
  justify-content: flex-start;
  align-items: center;
}

.list-flex__item:nth-child(3) .list-childs {
  justify-content: flex-start;
  align-items: flex-end;
}

.list-flex__item:nth-child(4) .list-childs {
  justify-content: center;
  align-items: flex-start;
}

.list-flex__item:nth-child(5) .list-childs {
  justify-content: flex-end;
  align-items: flex-start;
}

.list-flex__item:nth-child(6) .list-childs {
  justify-content: flex-end;
  align-items: stretch;
}

.list-flex__item:nth-child(7) .list-childs {
  justify-content: flex-end;
}

.list-flex__item:nth-child(8) .list-childs {
  justify-content: space-between;
}

.list-flex__item:nth-child(9) .list-childs {
  justify-content: space-around;
}

.list-flex__item:nth-child(10) .list-childs {
  justify-content: space-evenly;
}

.list-childs {
  position: relative;
  border: 1px solid #dcdcdc;
  border-radius: 2px;
}

.list-childs li {
  min-width: 10px;
  min-height: 10px;
  border: 1px solid black;
  background: orange;
  border-radius: 2px;
}

.list-childs:before {
  content: 'JC';
  color: #dcdcdc;
  font-size: 0.5rem;
  line-height: 0.5rem;
  height: 0.5rem;
  position: absolute;
  top: -0.5rem;
  right: 0;
}

.list-childs:after {
  content: 'AI';
  color: #dcdcdc;
  font-size: 0.5rem;
  line-height: 0.5rem;
  height: 0.5rem;
  position: absolute;
  bottom: -0.8rem;
  left: 0;
}/*
